}

.drawer {
  z-index: 100;
  position: fixed;
  width: 100%;
  padding: 0.6875rem 1.25rem;
  background: rgba(0, 0, 0, 0.5);
  height: 3.125rem;
  transition: ease all 0.3s;
}
.drawer.is__active {
  transition: ease all 0.5;
  background-color: #000;
}
.drawer__logo {
  font-size: 1.25rem;
  font-family: "Playlist", serif;
  color: #fff;
}
.drawer__icon {
  z-index: 101;
  position: fixed;
  width: 1.5rem;
  height: 0.75rem;
  right: 1.4375rem;
  top: 0.875rem;
}
.drawer__icon.is__active .drawer__icon-bar1 {
  transform: rotate(-45deg);
  top: 6px;
}
.drawer__icon.is__active .drawer__icon-bar2 {
  display: none;
}
.drawer__icon.is__active .drawer__icon-bar3 {
  transform: rotate(45deg);
  top: 6px;
}
.drawer__icon-bars {
  position: relative;
}
.drawer__icon-bar {
  transition: ease all 0.3s;
  background-color: #FFFFFF;
  width: 100%;
  display: block;
  height: 2px;
  position: absolute;
}
.drawer__icon-bar1 {
  top: 0;
}
.drawer__icon-bar2 {
  top: 0.375rem;
}
.drawer__icon-bar3 {
  top: 0.75rem;
}
.drawer__content {
  overflow: auto;
  width: 100vw;
  height: 100vh;
  position: fixed;
  right: 0;
  top: 3.125rem;
  background: #000;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.16);
  z-index: 299;
  transform: translateX(105%);
  transition: transform 0.5s ease 0s;
  opacity: 0;
}
.drawer__content.is__active {
  transform: translateX(0);
  opacity: 1;
}
.drawer__content-list {
  margin-top: 9.9375rem;
  margin-bottom: 13rem;
}
.drawer__content-item {
  text-align: center;
}
.drawer__content-item:not(:first-child) {
  margin-top: 2rem;
}
.drawer__content-item > a {
  color: #fff;
}

@media screen and (min-width: 768px) {
  .header {
    display: flex;
    align-items: center;
    position: fixed;
    width: 100%;
    height: 3.125rem;
    background: rgba(0, 0, 0, 0.5);
    padding-left: 2rem;
    z-index: 100;
  }
}
.header__logo-link {
  font-family: "Playlist", serif;
  color: #fff;
  font-size: 1.875rem;
}
.header__logo-link > img {
  display: block;
  height: 2.8125rem;
  width: 6.25rem;
  object-fit: cover;
  margin-top: 0.625rem;
}
.header__nav {
  display: flex;
  align-items: center;
  margin-left: auto;
}
.header__nav-list {
  display: flex;
  align-items: center;
}
.header__nav-item {
  margin-right: 2rem;
}
.header__nav-item > a {
  font-size: 1rem;
  color: #fff;
  letter-spacing: 2px;
}
.header__nav-item > a::after {
  position: absolute;
  left: 0;
  content: "";
  width: 100%;
  height: 2px;
  background: #000000;
  bottom: -1px;
  transform: scale(0, 1);
  transform-origin: center top;
  transition: transform 0.3s;
}
.header__contact-linkwrap {
  background-color: #fff;
  display: block;
  height: 100%;
}
.header__contact-link {
  font-size: 1rem;
  line-height: 3.125rem;
  text-align: center;
  display: block;
  letter-spacing: 2px;
  padding-left: 2rem;
  padding-right: 2rem;
  color: #111;
}

@keyframes zoomUp {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.15);
  }
}
.swiper-slide-active .slide-img,
.swiper-slide-duplicate-active .slide-img,
.swiper-slide-prev .slide-img {
  animation: zoomUp 10s linear 0s 1 normal both;
}

.slide-img img {
  display: block;
  height: 100vh;
}

